.map {
  background-image: url("../img/bg-map-mobile@1x.png");
  background-repeat: no-repeat;
  background-color: $white-two;
  background-size: cover;
  background-position: top -1015px right 0;
  height: 300px;
  position: relative;
}

.map::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -60px;
  width: 100%;
  height: 55px;
  background-image: url("../img/bg-triangle-white-footer.svg");
  background-repeat: no-repeat;
  background-size: contain;
  z-index: 1;
}

@media (max-width: $tablet-width) {
  .map {
    background-image: url("../img/bg-map-tablet@1x.jpg");
    background-position: top -1015px right 0;
    height: 377px;
  }
}

@media (min-width: $desktop-width) {
  .map {
    background-image: url("../img/bg-map-desktop@1x.jpg");
    background-position: top -370px right 0;
    height: 377px;
  }
}
